.ImgGrid {
    border: 2px 2px black;
    margin: 20px auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
}
.ImgWrap {
    overflow: hidden;
    height: 0;
    padding: 50% 0;
    position: relative;
    opacity: 0.8;
}
.ImgWrap img {
    width: 200px;
    min-width: 100%;
    min-height: 100%;
    max-width: 100%;
    /* max-height: 150%; */
    position: absolute;
    top: 0;
    left: 0;
}
